.imgMian {
  width: 300px;
  margin: 30px auto;
}
.imgMv {
  width: 100%;
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
}
.imgMv:hover {
  transform: scale(1.1);
}
.aB a:link {
  color: #666;
}
.aB a:visited {
  color: #f00;
}
.aB a:hover {
  color: #8cc8ff;
}
.aB a:active {
  color: #27ffc0;
}
.InputB input:enabled {
  background-color: #0f0;
}
.InputB input:disabled {
  background-color: red;
}
.demo {
  border: 1px solid #ccc;
  padding: 10px;
}
.demo li {
  float: left;
  list-style: none;
  padding: 2px;
  border: 1px solid #ccc;
  margin-right: 4px;
}
.demo a {
  display: block;
  width: 20px;
  text-align: center;
  border-radius: 50%;
  background: #fff819;
}
.demo li:nth-child(n+5) {
  background: #27ffc0;
}
.demo li:nth-last-of-type(3) {
  background: red;
}
.clearfix {
  clear: both;
  overflow: auto;
}
.weiYuanSu {
  border: 1px solid #666;
  width: 300px;
  margin: 0 auto;
  padding: 10px;
}
.weiYuanSu ::first-letter {
  font-size: 40px;
  font-weight: bold;
  float: left;
}
.weiYuanSu::first-line {
  color: #ff0;
  width: 300px;
}
.bAa {
  width: 300px;
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
}
.bAa::before {
  content: '';
  background: url("./imge/before.jpg") no-repeat center;
  background-size: 100% auto;
  display: block;
  height: 200px;
}
.bAa::after {
  content: '';
  background: url("./imge/after.jpg") no-repeat center;
  display: block;
  height: 200px;
  background-size: 100% auto;
}
.borderRadius {
  background-color: #ff6a33;
  margin: 20px auto;
  width: 200px;
  height: 100px;
  border: 1px solid #ccc;
  border-radius: 100px 100px 0 0;
}
.sanjiao {
  border-top: 50px solid transparent;
  border-bottom: 50px solid transparent;
  border-right: 50px solid #00f;
  width: 0px;
  height: 0px;
  margin: 10px auto;
}
.chart {
  background-color: #6A6;
  color: #fff;
  margin: 10px;
  width: 300px;
  height: 50px;
  line-height: 50px;
  padding: 0 25px;
  border-radius: 10px;
  position: relative;
}
.chart:before {
  content: '';
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 10px solid #6A6;
  position: absolute;
  left: -10px;
  top: 14px;
}
.diamond {
  margin: 10px auto;
  width: 50px;
  height: 50px;
  background-color: #66AA66;
  transform: rotate(45deg);
}
.parallel {
  margin: 20px auto;
  width: 100px;
  height: 50px;
  background-color: #66AA66;
  transform: skew(20deg);
}
.start {
  margin: 40px auto;
  width: 0;
  border-bottom: 35px solid red;
  border-right: 50px solid transparent;
  border-left: 50px solid transparent;
  transform: rotate(35deg);
  position: relative;
}
.start::before {
  content: '';
  width: 0;
  border-bottom: 40px solid red;
  border-right: 15px solid transparent;
  border-left: 15px solid transparent;
  transform: rotate(-35deg);
  position: absolute;
  top: -22.5px;
  left: -32.5px;
}
.start::after {
  content: '';
  position: absolute;
  border-bottom: 35px solid red;
  border-right: 50px solid transparent;
  border-left: 50px solid transparent;
  transform: rotate(-70deg);
  top: 1.5px;
  left: -52.5px;
}
.sixStart {
  margin: 40px auto;
  width: 0;
  border-bottom: 50px solid red;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  position: relative;
}
.sixStart::after {
  content: '';
  width: 0;
  border-top: 50px solid red;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  position: absolute;
  top: 15px;
  left: -25px;
}
.tiXing {
  margin: 40px auto;
  width: 150px;
  height: 150px;
  border-top: 50px solid #F00;
  border-left: 33px solid transparent;
  border-right: 33px solid transparent;
  border-bottom: 50px solid transparent;
  position: relative;
}
.tiXing::after {
  content: '';
  position: absolute;
  width: 0;
  border-bottom: 35px solid blue;
  border-right: 45px solid transparent;
  border-left: 45px solid transparent;
  top: -85px;
  left: -20px;
}
.sixBian {
  margin: 20px auto;
  width: 50px;
  height: 27.5px;
  background: red;
  position: relative;
}
.sixBian::before {
  content: '';
  position: absolute;
  border-bottom: 12.5px solid #f00;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  top: -12.5px;
}
.sixBian::after {
  content: '';
  border-top: 12.5px solid #f00;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  position: absolute;
  bottom: -12.5px;
}
.heart {
  position: relative;
  margin: 30px auto;
  width: 50px;
  height: 75px;
  background: #ff8bc3;
  border-radius: 25px 25px 0 0;
  transform: rotate(45deg);
}
.heart::after {
  content: '';
  position: absolute;
  width: 50px;
  height: 75px;
  background: #ff8bc3;
  border-radius: 25px 25px 0 0;
  transform: rotate(-90deg);
  left: -13px;
  top: 12px;
}
.egg {
  margin: 20px auto;
  width: 150px;
  height: 190px;
  background: #ffb422;
  border-radius: 50%;
}
.taiji {
  margin: 20px auto;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border-left: 100px solid #000c17;
  border-right: 1px solid #ccc;
  position: relative;
}
.taiji::before {
  background: #fff;
  content: '';
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 28px #000c17 solid;
  left: -50px;
  z-index: 100;
}
.taiji::after {
  background: #000c17;
  content: '';
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 28px #fff solid;
  left: -50px;
  top: 100px;
  z-index: 100;
}
.globCol {
  background: url("./imge/cc.png");
}
.bgFont {
  width: 300px;
  margin: 20px auto;
  background: #fff;
  border-radius: 10px;
  opacity: 0.5;
  padding: 10px;
  box-shadow: 2px 2px 4px #888;
}
.bgFont p {
  opacity: 1;
  line-height: 1.5em;
}
